<template>
  <div :id="id" :style="{ width: '100%', height: '100%' }"></div>
</template>

<script>
import * as echarts from 'echarts';
export default {
  name: "ring",
  props: {
    id: {
      type: String,
      default: "myChart",
    },
    dataList: {
      type: Array,
      default: () => [
        { value: 1048, name: '2023年到岗' },
        { value: 484, name: '2024总需求' },
        { value: 735, name: '2024已到岗' },
        { value: 300, name: '2024达成意向但未到岗' },
        { value: 580, name: '2024下半年计划招生' },
      ],
    },
  },
  data() {
    return {
      myChart: '',
      currentIndex: -1,
      changePieInterval: null,
      seriesData: this.dataList,
    };
  },
  watch: {
    dataList: {
      handler(newVal) {
        if (newVal) {
          this.seriesData = newVal.map(item => ({
            ...item,
            itemStyle: { color: item.itemStyle?.color || '#9BB9DD' }
          }));
          this.drawLine();
        }
      },
      deep: true
    }
  },
  mounted() {
    this.drawLine();
    this.startAutoHighlight(); // 开始自动高亮
  },
  methods: {
    drawLine() {
      if (this.myChart) {
        this.myChart.dispose();
        this.myChart = null;
      }
      // const myChart = echarts.init(document.getElementById(this.id));
      const chartContainer = document.getElementById(this.id);
      if (!chartContainer) {
        console.error(`Element with id ${this.id} not found`);
        return;
      }
      // 初始化图表
      this.myChart = echarts.init(chartContainer);

      const valueMap = {};
      const colorMap = {};
      this.dataList.forEach(item => {
        valueMap[item.name] = item.value;
        colorMap[item.name] = item.itemStyle?.color || '#9BB9DD'; // 默认颜色
      });

      const option = {
        tooltip: {
          trigger: 'item',
          backgroundColor: 'rgba(6,27,97,0.8)', // 半透明背景色
          borderColor: '#777',
          // borderWidth: 1,
          textStyle: {
            color: '#fff'
          }
        },
        legend: {
          orient: 'horizontal',
          right: '5%',
          top: 'center',
          itemGap: 30,
          align: 'left',
          width: '50%',
          itemWidth: 10,
          itemHeight: 10,
          selectedMode: false,
          // itemWidth: 0,
          // itemHeight: 0,
          textStyle: {
            fontFamily: 'Source Han Sans CN',
            fontWeight: 400,
            fontSize: '14px',
            color: '#9BB9DD',
            rich: {
              nameStyle: {
                fontWeight: 400,
                fontSize: 14,
                color: '#9BB9DD',
                padding: [0, 0, 0, 5]
              },
              valueStyle: {
                fontFamily: 'D-DIN-PRO',
                fontWeight: 'bold',
                fontSize: 18,
                color: '#DFECFB',
                padding: [5, 0, 0, 5]
              }
            },
          },
          formatter: (name) => {
            const value = valueMap[name] !== undefined ? valueMap[name] : '';
            return `{nameStyle|${name}}\n{valueStyle|${value}}`;
          },

        },
        series: [
          {
            // name: 'Inner Circle',
            type: 'pie',
            radius: '60%', // 内圈的尺寸
            center: ['22%', '50%'],
            avoidLabelOverlap: false,
            emphasis: {
              scale: false
            },
            label: {
              show: false
            },
            labelLine: {
              show: false
            },
            tooltip: {
              show: false
            },
            data: [
              { value: ' ', itemStyle: { color: 'rgba(16, 45, 115,0.55)' } }, // 深蓝色
            ]
          },
          {
            name: '宁波市金融行业就业情况总计统计分析',
            type: 'pie',
            radius: ['76%', '85%'],
            center: ['22%', '50%'],
            avoidLabelOverlap: false,

            padAngle: 5,
            label: {
              show: false,
              position: 'center',
              formatter: function (params) {
                const name = params.name.replace(/(.{8})/g, '$1\n');
                return `{nameStyle|${name}}\n{valueStyle|${params.value}}`;
              },
              rich: {
                nameStyle: {
                  fontWeight: 400,
                  fontSize: 14,
                  color: '#A8C3E6',
                },
                valueStyle: {
                  fontWeight: 'bold',
                  fontSize: 26,
                  color: '#DBE7F6',
                  padding: [15, 0, 0, 0]
                }
              }
            },
            emphasis: {
              label: {
                show: true,
                formatter: function (params) {
                  const name = params.name.replace(/(.{8})/g, '$1\n');
                  return `{nameStyle|${name}}\n{valueStyle|${params.value}}`;
                },
                rich: {
                  nameStyle: {
                    fontWeight: 400,
                    fontSize: 14,
                    color: '#A8C3E6',
                  },
                  valueStyle: {
                    fontWeight: 'bold',
                    fontSize: 26,
                    color: '#DBE7F6',
                    padding: [15, 0, 0, 0]
                  }
                }
              }
            },
            labelLine: {
              show: false
            },
            data: this.seriesData,
          },
          // 新增的中圈浅蓝圆环
          {
            // name: 'Middle Ring',
            type: 'pie',
            radius: ['60%', '70%'], // 中圈的尺寸
            center: ['22%', '50%'],
            avoidLabelOverlap: false,
            emphasis: {
              scale: false
            },
            label: {
              show: false
            },
            legend: {
              show: false
            },
            labelLine: {
              show: false
            },
            tooltip: {
              show: false
            },
            data: [
              { value: 1, itemStyle: { color: '#77B7F9' } }, // 浅蓝色
            ]
          }
        ]
      };

      this.myChart.setOption(option);
      this.selectPie();
      this.myChart.on('mouseover', this.handleMouseOver);
      this.myChart.on('mouseout', this.handleMouseOut);
    },
    startAutoHighlight() {
      this.changePieInterval = setInterval(() => {
        this.selectPie();
      }, 3000);
    },
    clearAutoHighlight() {
      if (this.changePieInterval) {
        clearInterval(this.changePieInterval);
        this.changePieInterval = null;
      }
    },
    handleMouseOver(params) {
      // console.log(params.seriesIndex);
      this.clearAutoHighlight();
      if (params.seriesIndex === 1) {
        this.currentIndex = params.dataIndex;
        this.highlightPie();
      }
    },
    handleMouseOut() {
      this.startAutoHighlight();
    },
    highlightPie() {
      const dataLen = this.seriesData.length;
      for (let idx = 0; idx < dataLen; idx++) {
        this.myChart.dispatchAction({
          type: 'downplay',
          seriesIndex: 1,
          dataIndex: idx
        });
      }
      this.myChart.dispatchAction({
        type: 'highlight',
        seriesIndex: 1,
        dataIndex: this.currentIndex
      });
    },
    selectPie() {
      this.currentIndex = (this.currentIndex + 1) % this.seriesData.length;
      this.highlightPie();
    }

  },
  beforeDestroy() {
    this.clearAutoHighlight();
    if (this.myChart) {
      this.myChart.dispose();
      this.myChart = null;
    }
  }
};
</script>