<template>
  <div>
    <div :id="id" :style="{ width: '100%', height: '100%' }" style="position: absolute;"></div>
    <div class="ring-img"></div>
  </div>
</template>

<script>
import * as echarts from 'echarts';
// import imgbg from '@/assets/img/Index/fen-zhi-jg-branch-bg.png';

export default {
  name: "Branch",
  props: {
    id: {
      type: String,
      default: "myChart",
    },
    dataList: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      myChart: null, // Store the chart instance
    };
  },
  watch: {
    dataList: {
      handler(newVal) {
        if (newVal) {
          this.drawLine();
        }
      },
      deep: true
    }
  },
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
      this.myChart = echarts.init(document.getElementById(this.id));

      const option = {
        tooltip: {
          trigger: 'item',
          backgroundColor: 'rgba(6,27,97,0.8)', // 半透明背景色
          borderColor: '#777',
          // borderWidth: 1,
          textStyle: {
            color: '#fff'
          }
        },
        series: [
          {
            name: '分支机构',
            type: 'pie',
            radius: ['80%', '70%'],
            avoidLabelOverlap: false,
            padAngle: 5,
            label: {
              show: true,
              position: 'outside',
              formatter: '{name|{b}}\n{value|{c}}',
              rich: {
                name: {
                  fontFamily: 'Source Han Sans CN',
                  fontWeight: 500,
                  fontSize: '16px',
                  color: '#C8DAFF',
                  padding: [10, 0],
                  fontStyle: 'italic',
                  width: 100,
                },
                value: {
                  fontFamily: 'Alibaba PuHuiTi',
                  fontWeight: 'bold',
                  color: '#C8DAFF',
                  fontStyle: 'italic',
                  fontSize: 28,
                }
              }
            },
            labelLine: {
              show: true,
              length: 10,
              length2: 60,
              lineStyle: {
                width: 1,
                type: 'solid'
              }
            },
            emphasis: {
              scale: false,
              label: {
                show: true,
                fontSize: 16,
                fontWeight: 'bold'
              }
            },
            data: [
              { value: 575, name: '机构总数', itemStyle: { color: '#0042FF' } },
              { value: 116, name: '单组建立党组织的', itemStyle: { color: '#007EFF' } },
              { value: 234, name: '党组织数', itemStyle: { color: '#00FF78' } },
            ].sort(function (a, b) {
              return a.value - b.value;
            }),
          }
        ],
        // graphic: [
        //   {
        //     type: 'image',
        //     id: 'logo',
        //     z: 100,
        //     style: {
        //       image: imgbg,
        //       opacity: 0.9,
        //       rotate: 0,
        //     },
        //     left: 237,
        //     top: 37,
        //     animation: true,
        //     animationDuration: 10000,
        //     animationEasing: 'linear',
        //     animationLoop: true,
        //     onFrame: function (params) {
        //       const percent = params.percent;
        //       this.style.rotate = percent * Math.PI * 2;
        //     }
        //   }
        // ]
      };

      this.myChart.setOption(option);

      window.addEventListener('resize', () => {
        this.myChart.resize();
      });
    }
  },
  beforeDestroy() {
    if (this.myChart) {
      this.myChart.dispose();
    }
  }
};
</script>
<style lang="less" scoped>
.ring-img {
  position: absolute;
  left: 236px;
  top: 37px;
  width: 150px;
  height: 150px;
  background: url("../../assets/img/Index/fen-zhi-jg-branch-bg.png") no-repeat
    center;
  background-size: 100% 100%;
  animation: rotate 4s linear infinite; /* 添加旋转动画 */
}
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>